.event {
    width: 100%;
    height: 100%;
    box-sizing: border-box;
    padding: 20px;
    background: #ccc;
    display: flex;
    justify-content: space-between;
    color: #000;
}

.dimission {
    width: 50%;
    height: 100%;
    background: #fff;
    box-sizing: border-box;
    padding: 10px;
}

.dimission>table {
    width: 100%;
}
.dimission>table .option>span:nth-child(1){
    color: red;
}
.dimission>table .option>span:nth-child(2){
    color: blue;
    margin-left:5px; 
}
.dimission>h2 {
    color: #000;
    font-size: 20px;
}

.ask {
    width: 50%;
    height: 100%;
    box-sizing: border-box;
    background: #fff;
    padding: 10px;
    margin-left:20px; 
}

.ask>h2 {
    color: #000;
    font-size: 20px;
}
.ask>table {
    width: 100%;
}
